// 测量标准模板
<template>
  <div class="typeMange">
    <!-- Header -->
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeHeader">
          <el-button type="primary" @click="addOne">新增</el-button>
        </div>
      </el-col>
    </el-row>

    


    <!-- Main -->
    <el-row>
      <el-col :span="24">
        <div class="grid-content bg-purple-dark typeMain">
          <!-- <p style="padding-bottom: 20px">测量标准模板</p> -->
          <div class="mb" style="padding-bottom: 20px;">
            <el-breadcrumb separator="/">
              <el-breadcrumb-item to="/measureStandard">测量标准</el-breadcrumb-item>
              <el-breadcrumb-item>测量标准模板</el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <el-table :data="tableData" style="width: 100%">
            <el-table-column label="道岔编号" width="180">
              <template slot-scope="scope">
                <span>{{ scope.row.dcbh }}</span>
              </template>
            </el-table-column>
            <el-table-column label="标准名称" width="580">
              <template slot-scope="scope">
                <span >{{ scope.row.measurementitem }}</span>
                <span v-show="false">{{ scope.row.id }}</span>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                <el-button v-if="scope.row.isdetails == 1" size="mini" type="" @click="seeList(scope.$index, scope.row)">查看</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
    <!-- Alert -->
    <div v-show="editStatus || addStatus" class="typeAlert-bg"></div>
    <div v-show="editStatus || addStatus" class="typeAlert">
      <el-row>
        <el-col>
          <p style="padding-bottom: 20px">{{status}}</p>
        </el-col>
      </el-row>
      <el-row class="edit-item">
        <el-col :span="6">
          <p>道岔编号：</p>
        </el-col>
        <el-col :span="18">
          <el-input v-model="save.dcbh" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>
      <el-row class="edit-item">
        <el-col :span="6">
          <p>标准名称：</p>
        </el-col>
        <el-col :span="18" style="display: flex; align-items: center;">
          <el-input v-model="save.measurementitem" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>


      <el-row class="edit-item">
        <el-col :span="6">
          <p>检查方法和数量：</p>
        </el-col>
        <el-col :span="18" style="display: flex; align-items: center;">
          <el-input v-model="save.inspectionmethod" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="6">
          <p>选择符号：</p>
        </el-col>
        <el-col :span="18">
          <el-dropdown trigger="click" @command="chooseFuhao">
            <span class="el-dropdown-link" style="cursor: pointer;">
              {{fuhao.calculation}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="item in fuhaoObj" :key="item.id" :command="item">{{item.calculation}}</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
            <el-button v-show="fuhao.id != -1" type="info" plain size="mini" style="margin-left: 10px;" @click="chooseShoudong">选择手动输入</el-button>
            <el-button v-show="fuhao.id == -1" type="primary" size="mini" style="margin-left: 10px;">当前为手动输入</el-button>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="6">
          <p>数据：</p>
        </el-col>
        <el-col :span="18" style="display: flex; align-items: center;">
          <p v-show="fuhao.id != -1" style="padding: 0 4px; border: 1px solid #DCDFE6; border-radius: 2px; margin-right: 5px;">{{fuhao.calculation}}</p>
          <el-input v-model="save.ranges" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="6">
          <p>检查意见：</p>
        </el-col>
        <el-col :span="18" style="display: flex; align-items: center;">
          <el-input v-model="save.standard1" placeholder="请输入内容"></el-input>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="8">
          <p>管理类别：</p>
        </el-col>
        <el-col :span="16" style="display: flex; align-items: center;">
          <template>
            <el-radio v-model="save.managementactegory" label="A">A</el-radio>
            <el-radio v-model="save.managementactegory" label="B">B</el-radio>
            <el-radio v-model="save.managementactegory" label="C">C</el-radio>
          </template>
        </el-col>
      </el-row>

      <el-row class="edit-item">
        <el-col :span="8">
          <p>是否有下一级：</p>
        </el-col>
        <el-col :span="16" style="display: flex; align-items: center;">
          <template>
            <el-radio v-model="save.isdetails" label="1">是</el-radio>
            <el-radio v-model="save.isdetails" label="0">否</el-radio>
          </template>
        </el-col>
      </el-row>

      <el-row >
        <el-col  v-show="editStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="editChange" type="primary">确定修改</el-button>
          <el-button size="mini" @click="editClose">放弃修改</el-button>
        </el-col>
        <el-col  v-show="addStatus" :span="24" class="edit-submit">
          <el-button size="mini" @click="addOk" type="primary">确定</el-button>
          <el-button size="mini" @click="editClose">放弃</el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import api from "@/api/index.js";
import { mapState, mapGetters } from "vuex";
  export default {
    data() {
      return {
        status: '',
        buttonType: 'info',
        turnoutstandardid: '', // 父级id
        radio: '2',
        fuhao: {
          calculation: '选择符号',
          id: '-1'
        },
        fuhaoObj: {},
        tableData: [],   // 表格数据
        editStatus: false,  // 修改弹窗显示
        save: {
          dcbh: '',     // 道岔编号
          turnoutstandardid: "", //道岔标准id
          measurementitem: '',//  测量项名称
          range1: '',   // 0 文字描述， 1 符号算法
          ranges: '',   // 输入的第一个数字
          isdetails: '0',// 是否有下一级 0 没有 1 有
          managementactegory: 'A', //管理类别
          inspectionmethod: '', // 检查方法和数量
          standard1: '',  //检查意见
          state: '',  // 是否为系统判断项点 0：手动判断 1：机器判断
        },
        edit: {             // 修改提交数据
          turnouttypename: '',
          reamke: '',  // 标准名称
          id: '',
        },
        addStatus: false,  // 新增弹窗显示
        add: {
          turnouttypename: '',
          reamke: ''
        }
      }
    },
    methods: {
      // 查看轨距列表
      seeList(index, row) {
        console.log(index, row);
        this.$router.push({ 
          name: 'gaugeMeasureStandard', 
          params: { turnoutstandardid:  row.id}
        })
      },
      // 选择手动输入
      chooseShoudong() {
        console.log('选择手动输入')
        this.fuhao = {
          calculation: '选择符号',
          id: '-1'
        }
      },
      // 选择符号
      chooseFuhao(choose) {
        console.log(choose)
        this.fuhao = choose;
      },
      // 新增
      addOne() {
        this.status = '新增'
        this.addStatus = true;
        this.save = {
          dcbh: '',     // 道岔编号
          turnoutstandardid: this.turnoutstandardid, //道岔标准id 【父级继承】
          measurementitem: '',//  测量项名称
          range1: '',   // 0 文字描述， 1 符号算法
          ranges: '',   // 输入的第一个数字
          isdetails: '0',// 是否有下一级 0 没有 1 有
          managementactegory: 'A', //管理类别
          inspectionmethod: '', // 检查方法和数量
          standard1: '',  //检查意见
          state: '',  // 是否为系统判断项点 0：手动判断 1：机器判断
        }
      },
      // 确认新增
      addOk() {
        console.log(this.save)
        console.log(this.fuhao)

        if(this.fuhao.id != "-1") {
          // 符号
          this.save.range1 = this.fuhao.id;
          this.save.state = "1";
        } else {
          // 手动输入
          this.save.range1 = "0";
          this.save.state = "0";
        }
        if(!this.save.dcbh) {
          this.$message({
            type: 'error',
            message: '输入不正确（道岔编号）!'
          });
          return;
        }
        if(!this.save.measurementitem) {
          this.$message({
            type: 'error',
            message: '输入不正确（标准名称）!'
          });
          return;
        }
        if(!this.save.ranges) {
          this.$message({
            type: 'error',
            message: '输入不正确（数据）!'
          });
          return;
        }
        let save = this.save;
        api.addMeasurementstandard(save).then(res => {
          this.editStatus = false;
          this.addStatus = false;
          console.log(res)
          if(res.data.code == 200) {
            this.$message({
              type: 'success',
              message: '新增成功!'
            });
            this.getLists(this.turnoutstandardid, '0');
          } else {
            this.$message({
              type: 'error',
              message: '新增失败!'
            });
          }
        })
      },
      // 唤起修改弹窗
      handleEdit(index, row) {
        this.status = '修改';
        console.log(index, row);
        this.editStatus = true;
        // this.edit.turnouttypename = row.turnouttypename;
        // this.edit.reamke = row.reamke;
        // this.edit.id = row.id;
        this.save = row
      },
      // 唤起删除弹窗
      handleDelete(index, row) {
        console.log(index, row);
        this.status = '删除'
        this.$confirm('此操作将永久删除该项, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          api.deleteMeasurementstandard('?id=' + row.id).then(res => {
            console.log(res)
            if(res.data == '1') {
              this.$message({
                type: 'success',
                message: '删除成功!'
              });
              this.getLists(this.turnoutstandardid, '0');
            } else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
      // 确认修改
      editChange() {
        this.editStatus = false;
        this.addStatus = false;
        api.editRoadClassify(this.edit.id, {turnouttypename: this.edit.turnouttypename, reamke: this.edit.reamke}).then(res => {
          console.log(res)
          if(res.data.status == 'ok') {
            this.$message({
              type: 'success',
              message: '修改成功!'
            });
            this.getLists();
          } else {
            this.$message({
              type: 'error',
              message: '修改失败!'
            });
          }
        })
      },
      // 取消修改
      editClose() {
        this.editStatus = false;
        this.addStatus = false;
      },
      // 获取列表
      getLists(id, pageNum) {
        api.getMeasurementstandard(id, pageNum).then(res => {
          console.log(res.data.result.list)
          this.tableData = res.data.result.list;
        })
      }
    },
    computed: {
        ...mapState({
            // recruitStatus: state => state.recruitStatus
            stateTurnoutstandardid: state => state.measureStandardTemplate.turnoutstandardid,
            test: state => state.test
        })
    },
    created() {
      console.log(this.$route.params)
      // 获取列表
      if(this.$route.params.turnoutstandardid) {
        this.$store.state.measureStandardTemplate.turnoutstandardid = this.$route.params.turnoutstandardid;
      }
      console.log(this.stateTurnoutstandardid)
      // this.getLists(this.$route.params.turnoutstandardid, '0');
      this.getLists(this.stateTurnoutstandardid, '0');
      // 获取算法
      api.getCalculationstandard().then(res => {
        var data = JSON.parse(res.data.result)
        console.log(data);
        this.fuhaoObj = data;
      })
    },
  }
</script>

<style lang="stylus" scoped>
  .typeHeader
      display flex
      justify-content flex-end
      padding-bottom 20px
  .typeMain
      background #fff
      border-radius 12px
      overflow hidden
      padding 20px
      .el-table
        border 1px solid rgba(0,0,0,.1)
  .typeAlert-bg
      width 100%
      height 100vh
      position fixed
      left 0
      top 0
      z-index 1
      background rgba(0,0,0,.5)
  .typeAlert
    width 530px
    height 470px
    background #fff
    position fixed
    left 50% 
    top 50% 
    transform translate(-50%, -50%)
    z-index 9
    padding 20px
    border-radius 4px
    .edit-item
      display flex
      align-items center
      padding 6px 0
    .edit-submit
      display flex
      justify-content flex-end
      padding-top 30px

</style>


